.section-price-cleaning--uslugis {
    position: relative;
    margin-top: 110px;
    padding-bottom: 100px;
    &::before {
        content: '';
        display: block;
        position: absolute;
        top: -75px;
        left: 0;
        width: 100%;
        bottom: 0;
        background: url(../img/section-price-cleaning--uslugis-bg.svg) no-repeat top center; }
    @include r($desktop) {
        margin-top: 80px; }
    @include r($tablet) {
        margin-top: 50px; }
    .container {
        position: relative;
        z-index: 2; }
    .section-title {
        max-width: 950px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 10px; }
    .subtitle {
        color: #4d4d4d;
        font-family: "MuseoCyrl";
        font-size: 17px;
        font-weight: 400;
        letter-spacing: 1.02px;
        text-align: center;
        margin-bottom: 4px; } }
.section-price-cleaning--uslugis__wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }
.section-price-cleaning--uslugis__item {
    margin-top: 30px;
    width: calc( 100% / 3 - 18.6px );
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    background-color: #ffffff;
    padding: 15px 15px 28px 15px;
    cursor: pointer;
    @include r($desktop) {
        padding: 5px 5px 10px 5px;
        margin-top: 20px; }
    @include r($tablet) {
        width: calc( 50% - 14px ); }
    @include r($mobile) {
        width: 100%; } }
.section-price-cleaning--uslugis__img {
    height: 275px;
    width: 100%;
    object-fit: cover;
    margin-bottom: 15px;
    border-radius: 10px;
    @include r($wide) {
        height: 190px; }
    @include r($desktop) {
        margin-bottom: 5px; }
    @include r($mobile) {
        height: 230px; } }
.section-price-cleaning--uslugis__txt {
    color: #562805;
    font-family: "MuseoCyrl";
    font-size: 20px;
    font-weight: 600;
    text-decoration: underline;
    letter-spacing: 1.2px;
    text-align: center;
    @include r($desktop) {
        font-size: 18px; } }
.price-table--uslugis {
    margin-top: 85px;
    position: relative;
    z-index: 2;
    .price-table__table {
        margin-bottom: 30px;
        border: 1px solid #00bdb6;
        position: relative;
        &:last-child {
            margin-bottom: 0; }
        &:not(:first-child) {
            .price-table__content {
                display: none; } } }
    .price-table__title-table {
        color: #00bdb6;
        background-color: transparent;
        transition: 0.3s; }
    .price-table__table.active {
        border: none;
        .price-table__title-table {
            color: #fff;
            background-color: #00bdb6; }
        .price-table__icon {
            svg {
                fill: #fff;
                transform: rotate(180deg); } } } }
.our-portfolio--uslugis {
    margin-top: 100px;
    position: relative;
    @include r($tablet) {
        margin-top: 50px; }
    &::before {
        top: -226px;
        left: 0;
        width: 100%;
        display: block;
        position: absolute;
        content: '';
        background: url(../img/our-portfolio--uslugis-bg.svg) no-repeat;
        height: 966px; } }
.order-cleaning-services2--uslugis {
    margin-top: 93px;
    position: relative;
    z-index: 2;
    @include r($tablet) {
        margin-top: 50px; } }
.faq--uslugis {
    margin-bottom: 0; }
.still-have-questions-contacts--uslugis {
    padding-bottom: 90px;
    padding-top: 0;
    margin-top: 105px;
    position: relative;
    @include r($tablet) {
        padding-bottom: 50px;
        margin-top: 50px; }
    &::before {
        content: '';
        display: block;
        position: absolute;
        top: -130px;
        left: 0;
        bottom: 0;
        width: 100%;
        background: url(../img/still-have-questions-contacts--office-bg.svg) no-repeat top center/cover; }
    .container {
        position: relative;
        z-index: 2; }
    h2 {
        text-align: left;
        @include r($tablet) {
            text-align: center; } } }
