.first-screen-index.first-screen--cleaning-flat {
    p {
        strong {
            font-weight: 700; }
        &:last-child {
            margin-top: 23px; } }
    .wrapper-txt {
        padding-top: 86px;
        @include r($wide) {
            padding-top: 0; } }
    .decor-img {
        background-image: url(../img/first-screen--cleaning-flat.png); } }
.calculate-cost.calculate-cost--cleaning-flat {
    margin-top: 204px;
    @include r(1600) {
        margin-top: 100px; }
    @include r($desktop) {
        margin-top: 50px; } }
.advantages-index.advantages--cleaning-flat {
    position: relative;
    z-index: 2; }
.price-table--cleaning-flat {
    margin-top: 57px;
    padding-bottom: 112px;
    position: relative;
    &::before {
        position: absolute;
        content: '';
        width: 100%;
        left: 0;
        top: -690px;
        bottom: 0;
        background: url(../img/price-table--cleaning-flat-bg.svg) no-repeat top center / cover; }
    @include r($tablet) {
        margin-top: 20px;
        padding-bottom: 50px; }
    .container {
        position: relative;
        z-index: 2; } }
.order-cleaning-services2--cleaning-flat {
    .container {
        padding-top: 79px;
        padding-bottom: 100px;
        border-bottom: 1px solid rgba(#000,0.2);
        @include r($tablet) {
            padding-top: 50px;
            padding-bottom: 50px; } } }
.our-portfolio--cleaning-flat {
    margin-top: 97px;
    position: relative;
    &::before {
        content: '';
        display: block;
        position: absolute;
        top: -236px;
        left: 0;
        width: 100%;
        height: 966px;
        background: url(../img/our-portfolio--cleaning-flat-bg.svg) no-repeat top center / cover; }
    @include r($tablet) {
        margin-top: 50px; } }
.faq--cleaning-flat {
    position: relative;
    z-index: 2;
    margin-top: 98px;
    margin-bottom: 90px;
    @include r($tablet) {
        margin-top: 50px;
        margin-bottom: 40px; } }
