.order-cleaning-services2 {
    .section-title {
        margin-bottom: 20px; }
    .subtitle {
        color: #4d4d4d;
        font-family: "VisbyRoundCF";
        font-size: 25px;
        font-weight: 700;
        letter-spacing: 1.5px;
        max-width: 850px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
        @include r($mobile) {
            font-size: 20px;
            letter-spacing: 0;
            font-weight: 400; } } }
.order-cleaning-services2__wrap {
    max-width: 1030px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 26px;
    @include r($mobile) {
        margin-top: 10px; }
    form {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between; }
    input[type="text"], input[type="tel"] {
        width: 100%;
        height: 50px;
        padding-left: 0;
        border-radius: 0;
        border: none;
        border-bottom: 1px solid rgba(0, 0, 0, 0.2);
        color: #4d4d4d;
        letter-spacing: 1.08px;
        background-color: transparent; }
    input[type="text"] {
        font-family: "VisbyRoundCF";
        font-size: 18px;
        font-weight: 300;
        line-height: 27px; }
    input[type="tel"] {
        font-family: "MuseoCyrl";
        font-size: 18px;
        font-weight: 300;
        line-height: 22px; } }
.order-cleaning-services2__name, .order-cleaning-services2__tel {
    width: calc( 50% - 10px );
    @include r($mobile) {
        width: 100%; } }
.order-cleaning-services2__politic, .order-cleaning-services2__submit {
    width: 100%; }
.order-cleaning-services2__politic {
    color: #4d4d4d;
    font-family: "MuseoCyrl";
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0.96px;
    text-align: center;
    margin-top: 44px;
    margin-bottom: 45px;
    @include r($mobile) {
        margin: 20px 0; } }
.order-cleaning-services2__submit {
    input[type="submit"] {
        max-width: 280px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        cursor: pointer; } }

