* {
  box-sizing: border-box;
  outline: none; }

body, html {
  background: white;
  color: $fontColor;
  font-family: $font-family;
  font-size: 16px;
  min-width: 320px;
  max-width: 100%;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.page-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  max-width: 1920px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden; }
.container {
  max-width: 1180px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  @include r($wide) {
    max-width: 970px; }
  @include r($desktop) {
    max-width: 750px; }
  @include r($tablet) {
    padding-left: 10px;
    padding-right: 10px; } }
button {
  padding: 0; }
h1,h2,h3,h4,h5,h6,p,a,span,ul,ol,li {
  padding: 0;
  margin: 0; }
.main-title {
  color: #562805;
  font-family: "VisbyRoundCF";
  font-size: 50px;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 3px;
  position: relative;
  padding-bottom: 28px;
  @include r($desktop) {
    font-size: 45px;
    padding-bottom: 24px;
    text-align: center; }
  @include r($tablet) {
    font-size: 38px;
    padding-bottom: 20px; }
  @include r($mobile) {
    font-size: 34px; }
  &::after {
    position: absolute;
    bottom: 0;
    left: 0;
    content: '';
    display: block;
    width: 168px;
    height: 5px;
    background-color: #00bdb6;
    @include r($desktop) {
      left: 50%;
      transform: translateX(-50%); } } }
h2 {
  color: #562805;
  font-family: "VisbyRoundCF";
  font-size: 40px;
  font-weight: 900;
  letter-spacing: 2.4px;
  text-align: center;
  @include r($mobile) {
    font-size: 30px; } }
a {
  color: $fontColor;
  text-decoration: none;
  font-family: $font-family;
  font-size: 18px;
  font-weight: 400;
  &:hover, &:focus {
    text-decoration: none; } }
ul {
  list-style: none;
  li {} }
ol {
  list-style: decimal; }
img.svg {
	display: none; }
.wrapper-slider {
  position: relative; }
.swiper-button-next, .swiper-button-prev {
  transform: none;
  margin: 0;
  padding: 0;
  top: auto;
  bottom: auto;
  left: auto;
  right: auto;
  background: none;
  color: transparent;

  width: 36px;
  height: 65px;
  @include r($tablet) {
    width: 28px;
    height: 55px; }
  &::after {
    display: none; }
  svg {
    width: 100%;
    height: 100%;
    display: block;
    fill: #562805; } }
.swiper-button-prev {
  svg {
    transform: rotate(180deg); } }
.first-screen-section-page {
  padding-top: 230px;
  @include r($wide) {
    padding-top: 150px; }
  @include r($tablet) {
    padding-top: 120px; } }
