.review-txt-container {
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    background-color: #ffffff;
    padding: 35px 0 27px 0;
    @include r($tablet) {
        padding: 15px 0 25px 0; } }
.review-txt-container__top-line {
    padding: 0 20px;
    margin-bottom: 25px;
    @include r($tablet) {
        padding: 0 10px;
        margin-bottom: 15px; }
    p {
        color: #4d4d4d;
        font-family: "MuseoCyrl";
        font-size: 17px;
        font-weight: 400;
        line-height: 24px;
        letter-spacing: 1.02px;
        text-align: center; } }
.review-txt-container__bottom-line {
    display: flex;
    margin: 0 30px;
    padding-top: 25px;
    border-top: 1px solid rgba(#000,0.1);
    @include r($tablet) {
        flex-direction: column;
        align-items: center;
        text-align: center;
        margin: 0 15px; } }
.review-txt-container-item-bottom-line {
    max-width: 200px;
    width: 100%;
    margin-bottom: 10px;
    &:last-child {
        margin-bottom: 0; } }
.review-txt-container__cleaner {
    margin-left: 150px;
    @include r($desktop) {
        margin-left: 50px; }
    @include r($tablet) {
        margin-left: 0; } }
.review-txt-container__assessment {
    margin-left: 105px;
    @include r($desktop) {
        margin-left: 50px; }
    @include r($tablet) {
        margin-left: 0; } }
.review-txt-container__title-bottom-line, .review-txt-container__txt-bottom-line {
    font-family: "MuseoCyrl";
    font-size: 17px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 1.02px; }
.review-txt-container__title-bottom-line {
    color: #999999;
    margin-bottom: 12px; }
.review-txt-container__txt-bottom-line {
    color: #4d4d4d;
    strong {
        margin-right: 15px; } }
.review-txt-container-img-bottom-line {
    max-width: 100%;
    height: auto; }



