.video-container-review {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }
.video-container-review__block {
    width: calc( 100% / 3 - 18px );
    height: 290px;
    border-radius: 20px;
    overflow: hidden;
    position: relative;
    margin: 15px 0;
    @include r($desktop) {
        height: 200px; }
    @include r($tablet) {
        width: 100%;
        height: 300px; }
    @include r($mobile) {
        height: 250px; }
    iframe {
        z-index: 2;
        position: absolute;
        top: 0;
        left: 0;
        width: 100% !important;
        height: 100% !important; } }
.video-container-review__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block; }
.video-container-review__play {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 71px;
    height: 71px;
    background-color: rgba(#000000,0.1);
    border-radius: 50%;
    opacity: 0.8;
    transition: 0.3s;
    &::before {
        display: block;
        content: '';
        position: absolute;
        z-index: 1;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 51px;
        height: 51px;
        background-color: #000000;
        opacity: 0.1;
        border-radius: 50%; }
    &::after {
        display: block;
        content: url(../img/play.svg);
        width: 17px;
        position: absolute;
        z-index: 2;
        top: calc( 50% + 2px );
        left: calc( 50% + 2px );
        transform: translate(-50%,-50%); }
    &:hover {
        opacity: 1; } }
