.first-screen--services {
    p {
        strong {
            font-weight: 700; }
        &:last-child {
            margin-top: 23px; } } }
.section-price-cleaning--services {
    margin-top: 100px;
    position: relative;
    padding-bottom: 98px;
    margin-bottom: 10px;
    @include r($tablet) {
        margin-top: 50px;
        padding-bottom: 50px; }
    &::before {
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        top: -80px;
        bottom: 0;
        background: url(../img/section-price-cleaning--services-bg.svg) no-repeat top center; }
    .section-title {
        max-width: 950px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 10px; }
    .subtitle {
        color: #4d4d4d;
        font-family: "MuseoCyrl";
        font-size: 17px;
        font-weight: 400;
        letter-spacing: 1.02px;
        text-align: center;
        margin-bottom: 4px; }
    .container {
        position: relative;
        z-index: 2; } }
.section-price-cleaning--services__wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }
.section-price-cleaning--services__img {
    height: 275px;
    width: 100%;
    object-fit: cover;
    margin-bottom: 15px;
    border-radius: 10px;
    @include r($wide) {
        height: 190px; }
    @include r($desktop) {
        margin-bottom: 5px; }
    @include r($mobile) {
        height: 230px; } }
.section-price-cleaning--services__item {
    margin-top: 30px;
    width: calc( 100% / 3 - 18.6px );
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    background-color: #ffffff;
    padding: 15px 15px 28px 15px;
    cursor: pointer;
    @include r($desktop) {
        padding: 5px 5px 10px 5px;
        margin-top: 20px; }
    @include r($tablet) {
        width: calc( 50% - 14px ); }
    @include r($mobile) {
        width: 100%; }
    li {
        margin-bottom: 18px;
        &:last-child {
            margin-bottom: 0; }
        a {
            font-family: "MuseoCyrl";
            font-size: 17px;
            letter-spacing: 1.02px;
            color: #00bdb6;
            transition: 0.3s;
            @include r($wide) {
                font-size: 16px;
                letter-spacing: 0; }
            @include r($desktop) {
                font-size: 15px; }
            strong {
                color: #4d4d4d;
                font-weight: 600; }
            &:hover {
                text-decoration: underline;
                color: #4d4d4d;
                strong {
                    color: #4d4d4d; } } } } }
.section-price-cleaning--services__txt {
    color: #562805;
    font-family: "MuseoCyrl";
    font-size: 20px;
    font-weight: 600;
    text-decoration: underline;
    letter-spacing: 1.2px;
    margin-bottom: 22px;
    @include r($desktop) {
        font-size: 18px; } }
.order-cleaning-services2--services {
    .container {
        padding-top: 100px;
        padding-bottom: 100px;
        border-bottom: 1px solid rgba(#000,0.2);
        @include r($tablet) {
            padding-top: 0;
            padding-bottom: 50px; } } }
.still-have-questions-contacts--services {
    padding-bottom: 0;
    @include r($tablet) {
        padding-top: 0;
        padding-bottom: 30px; } }

