.faq {
    margin-top: 96px;
    margin-bottom: 95px;
    @include r($tablet) {
        margin-top: 50px;
        margin-bottom: 50px; }
    h2 {
        margin-bottom: 28px;
        @include r($mobile) {
            font-size: 25px; } }
    .wrapper-accordions {
        max-width: 730px;
        width: 100%;
        margin-left: auto;
        margin-right: auto; }
    .accordion.active {
        h4 {
            color: #4d4d4d; }
        svg {
            fill: #4d4d4d;
            transform: rotate(180deg); } }
    .accordion {
        padding-bottom: 23px;
        margin-bottom: 23px;
        border-bottom: 1px solid rgba(#000,0.1);
        position: relative;
        &:last-child {
            margin-bottom: 0; } }
    .visible-area {
        cursor: pointer;
        padding-right: 68px;
        @include r($tablet) {
            padding-right: 30px; } }
    .slide-area {
        padding-right: 68px;
        margin-top: 13px;
        display: none;
        @include r($tablet) {
            padding-right: 30px; } }
    .arrow-accordion-faq {
        position: absolute;
        top: 3px;
        right: 0;
        cursor: pointer; }
    svg {
        width: 11px;
        height: 6px;
        fill: #00bdb6;
        transition: 0.3s; }
    h4 {
        font-weight: 700;
        color: #00bdb6;
        transition: 0.3s; }
    h4, p {
        font-family: "VisbyRoundCF";
        font-size: 18px;
        line-height: 27px;
        letter-spacing: 1.08px; }
    p {
        margin-bottom: 15px;
        &:last-child {
            margin-bottom: 0; } } }
