.house-order-longer-your-concern {
    h2 {
        text-align: left;
        @include r($desktop) {
            text-align: center; } }
    .wrapper-items {
        display: flex;
        @include r($desktop) {
            flex-direction: column;
            align-items: center; } }
    .wrapper-txt {
        max-width: 655px;
        width: 100%;
        padding-right: 40px;
        margin-top: 24px;
        @include r($desktop) {
            padding-right: 0; } }
    .wrapper-img {
        max-width: 676px;
        width: 100%;
        margin-right: -150px;
        margin-top: 68px;
        @include r($desktop) {
            margin-right: 0;
            margin-top: 30px; }
        img {
            width: 100%;
            height: auto;
            display: block; } }
    p, li {
        color: #4d4d4d;
        font-family: "VisbyRoundCF";
        font-size: 18px;
        font-weight: 400;
        line-height: 27px;
        letter-spacing: 1.08px;
        margin-bottom: 23px;
        &:last-child {
            margin-bottom: 0; } }
    p {
        font-weight: 300; }
    p strong, li {
        font-weight: 700; }
    li {
        padding-left: 74px;
        position: relative;
        @include r($tablet) {
            padding-left: 45px; }
        &::before {
            content: url(../img/arrow-ic.svg);
            width: 37px;
            position: absolute;
            top: 7px;
            left: 0; } }
    ul, ol {
        margin-bottom: 23px;
        &:last-child {
            margin-bottom: 0; } } }
