.container_still-have-questions-contacts {
    display: flex;
    justify-content: space-between;
    @include r($desktop) {
        flex-direction: column;
        align-items: center; }
    .wrapper-txt, .wrapper-img {
        width: 100%; }
    .wrapper-txt {
        max-width: 580px;
        @include r($wide) {
            max-width: 500px; } }
    .wrapper-img {
        max-width: 607px;
        width: 100%;
        margin-right: -45px;
        margin-top: 40px;
        @include r($wide) {
            max-width: 495px; }
        @include r($desktop) {
            margin-right: 0; } }
    img {
        width: 100%;
        height: auto;
        display: block; }
    h2 {
        margin-bottom: 28px;
        @include r($desktop) {
            text-align: center; } }
    .desc-title-section {
        color: #4d4d4d;
        font-family: "VisbyRoundCF";
        font-size: 25px;
        font-weight: 700;
        letter-spacing: 1.2px;
        margin-bottom: 15px;
        @include r($desktop) {
            text-align: center; }
        @include r($mobile) {
            font-size: 20px;
            letter-spacing: 0.5px; } }
    input[type="text"],input[type="tel"],input[type="email"] {
        height: 50px; }
    input[type="text"],input[type="tel"],input[type="email"], textarea {
        padding-left: 0;
        color: #4d4d4d;
        font-family: "VisbyRoundCF";
        font-size: 18px;
        font-weight: 300;
        letter-spacing: 1.08px;
        border-radius: 0;
        border: none;
        border-bottom: 1px solid rgba(#000000,0.2);
        background-color: transparent; }
    input[type="tel"] {
        font-family: "MuseoCyrl"; }
    .textarea {
        margin-bottom: 35px; }
    textarea {
        resize: none;
        height: 105px;
        @include r($tablet) {
            height: 70px; } }
    .politic {
        color: #4d4d4d;
        font-family: "MuseoCyrl";
        font-size: 16px;
        font-weight: 400;
        line-height: 22px;
        letter-spacing: 0.96px;
        text-align: center;
        margin-bottom: 40px;
        @include r($tablet) {
            margin-bottom: 20px; } }
    input[type="submit"] {
        max-width: 280px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        cursor: pointer;
        border: none; } }

