.header {
    position: fixed;
    z-index: 100;
    max-width: 1920px;
    width: 100%;
    .container {
        padding-top: 8px;
        display: flex;
        justify-content: space-between;
        transition: 0.3s;
        @include r($wide) {
            padding-bottom: 8px; }
        @include r($desktop) {
            padding-right: 70px; }
        @include r($mobile) {
            padding-right: 40px; } }
    .wrapper-logo {
        max-width: 210px;
        width: 100%;
        transition: 0.3s;
        @include r($wide) {
            max-width: 97px; }
        @include r($desktop) {
            position: relative;
            z-index: 2; }
        @include r($mobile) {
            max-width: 65px; } }
    img {
        display: block;
        width: 100%;
        height: auto;
        max-height: 167px;
        transition: 0.3s;
        @include r($wide) {
            max-height: 77px; } }
    .nav-header.active {
        @include r($desktop) {
            transform: translate(0);
            box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1); } }
    .nav-header {
        padding-top: 53px;
        max-width: 600px;
        width: 100%;
        transition: 0.3s;
        @include r($wide) {
            padding-top: 27px; }
        @include r($desktop) {
            position: absolute;
            top: 0;
            left: 0;
            max-width: 100%;
            padding-top: 100px;
            background-color: #fff;
            transform: translateX(calc( -100% - 300px )); } }
    .main-menu-header {
        display: flex;
        justify-content: space-between;
        transition: 0.3s;
        @include r($wide) {
            margin-left: 35px; }
        @include r($desktop) {
            flex-direction: column;
            padding-bottom: 30px;
            margin-left: 15px;
            max-height: calc( 100vh - 96px );
            overflow-y: auto; }
        & > li {
            position: relative;
            padding-bottom: 28px;
            @include r($desktop) {
                padding-bottom: 0px;
                margin-bottom: 15px;
                &:last-child {
                    margin-bottom: 0; } }
            &:hover {
                & > a {
                    color: #00bdb6;
                    &::after {
                        opacity: 1; } }
                @include rmin($desktop) {
                    & > .sub-menu {
                        display: flex; } } }
            & > a {
                color: #4d4d4d;
                font-family: "MuseoCyrl";
                font-size: 18px;
                font-weight: 300;
                line-height: 22px;
                letter-spacing: 1.08px;
                transition: 0.3s;
                position: relative;
                &::after {
                    position: absolute;
                    bottom: -7px;
                    left: 0;
                    content: '';
                    display: block;
                    width: 70px;
                    height: 3px;
                    opacity: 0;
                    background-color: #00bdb6;
                    transition: 0.3s;
                    @include r($desktop) {
                        display: none; } } }
            & > .sub-menu {
                position: absolute;
                top: 100%;
                background-color: #ffffff;
                box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1), inset 0 3px 0 #00bdb6;
                border-radius: 20px;
                display: none;
                @include rmin($desktop) {
                    justify-content: space-between;
                    flex-wrap: wrap; }
                @include r($desktop) {
                    position: static;
                    background-color: #fff;
                    box-shadow: none; }
                &::before {
                    content: '';
                    display: block;
                    position: absolute;
                    bottom: 100%;
                    border-bottom: 8px solid #00bdb6;
                    border-left: 8px solid transparent;
                    border-right: 8px solid transparent;
                    @include r($desktop) {
                        display: none; } }
                & > li {
                    width: 100%;
                    & > a {
                        color: #4d4d4d;
                        font-family: "VisbyRoundCF";
                        font-size: 24px;
                        font-weight: 700;
                        letter-spacing: 1.44px;
                        @include r($desktop) {
                            font-size: 18px; }
                        &:hover {
                            text-decoration: underline; } } } } }
        .menu-item-1 {
            & > .sub-menu {
                left: -271px;
                width: 1180px;
                padding: 3px 55px 7px 24px;
                @include rmin($desktop) {
                    transition: 0.3s; }
                @include r($wide) {
                    width: 970px;
                    left: -144px; }
                @include r($desktop) {
                    width: calc( 100% - 10px );
                    padding: 10px; }
                &::before {
                    left: 308px;
                    @include r($wide) {
                        left: 195px; } }
                & > .menu-item-has-children {
                    max-width: 355px;
                    margin: 30px 0;
                    @include r($desktop) {
                        margin: 0 0 20px 0;
                        &:last-child {
                            margin-bottom: 0; } }
                    a {
                        &:hover {
                            text-decoration: underline; } }
                    & > .sub-menu {
                        margin-top: 11px;
                        @include r($desktop) {
                            display: none; }
                        li {
                            position: relative;
                            margin-bottom: 15px;
                            padding-left: 20px;
                            &::before {
                                position: absolute;
                                top: 8px;
                                left: 0;
                                content: '';
                                display: block;
                                width: 6px;
                                height: 6px;
                                background-color: #00bdb6; }
                            &:last-child {
                                margin-bottom: 0; } }
                        a {
                            color: #4d4d4d;
                            font-family: "VisbyRoundCF";
                            font-size: 18px;
                            font-weight: 500;
                            letter-spacing: 1.08px; } } } } }
        .menu-item-2 {
            & > .sub-menu {
                left: -22px;
                width: 697px;
                padding: 15px 51px 25px 42px;
                @include rmin($desktop) {
                    transition: 0.3s; }
                @include r($desktop) {
                    width: calc( 100% - 10px );
                    padding: 10px; } }
            li {
                width: 100%;
                max-width: 280px;
                margin: 15px 0 15px 0;
                @include r($desktop) {
                    margin: 0 0 20px 0;
                    &:last-child {
                        margin-bottom: 0; } } } } }
    .arrow-menu {
        display: none;
        background: none;
        @include r($desktop) {
            display: inline-block;
            margin-left: 10px;
            transform: translateY(2px);
            width: 20px;
            height: 13px;
            background: url(../img/arrow.svg) no-repeat center; } }
    .wrapper-connection {
        margin-top: 34px;
        max-width: 248px;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        transition: 0.3s;
        @include r($wide) {
            margin-top: 7px; }
        @include r($desktop) {
            position: relative;
            z-index: 2; }
        @include r($mobile) {
            // display: none
            margin-top: 15px;
            margin-left: 7px; } }
    .top-line {
        width: 100%;
        display: flex;
        justify-content: space-between;
        @include r($mobile) {
            justify-content: flex-start;
            align-items: center; }
        a {
            color: #562805;
            font-family: "MuseoCyrl";
            font-size: 18px;
            font-weight: 700;
            letter-spacing: 1.08px;
            @include r($mobile) {
                letter-spacing: 0;
                font-size: 16px; }
            &:nth-child(1) {
                @include r($mobile) {
                    margin-right: 5px;
                    margin-top: 3px; } }
            &:nth-child(2) {
                @include r($mobile) {
                    display: none; } } } }
    button {
        color: #562805;
        font-family: "MuseoCyrl";
        font-size: 18px;
        font-weight: 300;
        letter-spacing: 1.08px;
        text-decoration: underline;
        @include r($mobile) {
            display: none; } }
    svg {
        width: 22px;
        height: 22px;
        fill: #00bdb6; } }
