.first-screen-index.first-screen-office {
    .wrapper-links {
        margin-bottom: 150px;
        @include r($wide) {
            margin-bottom: 75px; }
        @include r($desktop) {
            margin-bottom: 45px; } } }
.advantages-office.advantages-index {
    margin-top: 204px;
    @include r(1600) {
        margin-top: 100px; }
    @include r($wide) {
        margin-top: 50px; } }
.price-table-office {
    padding-top: 100px;
    padding-bottom: 100px;
    background: url(../img/price-table-bg.svg) no-repeat bottom center / cover;
    @include r($wide) {
        padding: 80px 0; }
    @include r($tablet) {
        padding: 45px 0; } }
.steps-work.steps-work-office {
    padding: 100px 0;
    @include r($tablet) {
        padding: 50px 0; }
    .container {
        padding-bottom: 0;
        border: none; } }
.order-cleaning-services--office {
    position: relative;
    margin-bottom: 95px;
    @include r($desktop) {
        margin-bottom: 50px; }
    &::before {
        content: '';
        display: block;
        position: absolute;
        top: -245px;
        left: 0;
        width: 100%;
        height: 1798px;
        background: url(../img/order-cleaning-services--office-bg.svg) no-repeat top center / cover; } }
.order-cleaning-services--office__wrap {
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 2;
    padding-bottom: 86px;
    border-bottom: 1px solid rgba(#000,0.1);
    @include r($desktop) {
        flex-direction: column;
        align-items: center;
        padding-bottom: 45px; } }
.order-cleaning-services--office__form {
    max-width: 580px;
    width: 100%;
    padding: 0 27px;
    @include r($wide) {
        padding: 0 5px; }
    @include r($desktop) {
        padding: 0;
        margin-bottom: 30px; }
    h2 {
        margin-bottom: 28px;
        @include r($desktop) {
            text-align: center; } }
    .desc-title-section {
        color: #4d4d4d;
        font-family: "VisbyRoundCF";
        font-size: 25px;
        font-weight: 700;
        letter-spacing: 1.2px;
        margin-bottom: 15px;
        text-align: center;
        @include r($mobile) {
            font-size: 20px;
            letter-spacing: 0.5px; } }
    form {
        max-width: 505px;
        width: 100%;
        margin-left: auto;
        margin-right: auto; }
    .ui-group.tel {
        margin-bottom: 45px; }
    input[type="text"],input[type="tel"],input[type="email"] {
        height: 50px; }
    input[type="text"],input[type="tel"],input[type="email"] {
        padding-left: 0;
        color: #4d4d4d;
        font-family: "VisbyRoundCF";
        font-size: 18px;
        font-weight: 300;
        letter-spacing: 1.08px;
        border-radius: 0;
        border: none;
        border-bottom: 1px solid rgba(#000000,0.2);
        background-color: transparent; }
    input[type="tel"] {
        font-family: "MuseoCyrl"; }
    .politic {
        color: #4d4d4d;
        font-family: "MuseoCyrl";
        font-size: 16px;
        font-weight: 400;
        line-height: 22px;
        letter-spacing: 0.96px;
        text-align: center;
        margin-bottom: 40px;
        @include r($tablet) {
            margin-bottom: 20px; } }
    input[type="submit"] {
        max-width: 280px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        cursor: pointer;
        border: none; }
    .submit {
        margin-bottom: 0; } }
.order-cleaning-services--office__img {
    max-width: 607px;
    width: 100%;
    margin-right: -45px;
    @include r($desktop) {
        margin-right: 0; }
    img {
        width: 100%;
        height: auto;
        display: block; } }
.our-portfolio--office {
    padding-bottom: 121px;
    @include r($wide) {
        padding-bottom: 80px; }
    @include r($desktop) {
        padding-bottom: 50px; } }
.section-review.section-review--office {
    padding-bottom: 0;
    .btn {
        margin-top: 100px;
        @include r($tablet) {
            margin-top: 50px; } } }
.faq--office {
    padding-top: 0;
    margin-top: 85px;
    position: relative;
    z-index: 2;
    @include r($tablet) {
        margin-top: 45px; } }
.still-have-questions-contacts--office {
    position: relative;
    padding: 0 0 72px 0;
    margin-bottom: 0;
    &::before {
        content: '';
        display: block;
        position: absolute;
        top: -160px;
        left: 0;
        bottom: 0;
        width: 100%;
        background: url(../img/still-have-questions-contacts--office-bg.svg) no-repeat top center / cover; }
    .container {
        z-index: 2;
        position: relative; } }
