.first-screen-index.first-screen--usluga {
    .wrapper-txt {
        @include r($wide) {
            display: flex;
            flex-direction: column;
            align-items: center; } }
    .decor-img {
        background-image: url(../img/first-screen-usluga.png);
        @include r($wide) {
            background: none; } }
    h1 {
        margin-bottom: 29px;
        &::after {
            bottom: -12px; } }
    li {
        color: #4d4d4d;
        font-family: "VisbyRoundCF";
        font-size: 19px;
        font-weight: 500;
        letter-spacing: 1.2px;
        margin-bottom: 17px;
        padding-left: 22px;
        position: relative;
        &::before {
            content: '';
            display: block;
            position: absolute;
            top: 8px;
            left: 0;
            width: 6px;
            height: 6px;
            background-color: #00bdb6; }
        &:last-child {
            margin-bottom: 0; } }
    ul {
        margin-bottom: 27px; }
    .price {
        color: #4d4d4d;
        font-family: "VisbyRoundCF";
        font-size: 23px;
        font-weight: 900;
        letter-spacing: 1.38px;
        margin-bottom: 50px;
        @include r($tablet) {
            margin-bottom: 25px; } }
    .section-title {
        text-align: left;
        font-size: 33px;
        letter-spacing: 2.1px;
        @include r($tablet) {
            font-size: 27px;
            letter-spacing: 1px;
            text-align: center;
            margin-bottom: 10px; } }
    .desc-section-title {
        margin-top: 5px;
        font-size: 20px;
        letter-spacing: 1.2px;
        margin-bottom: 23px; } }
.calculate-cost--usluga {
    margin-top: -5px;
    .ui-group {
        width: 100%;
        @include r($wide) {
            width: calc( 100% / 3 ); }
        @include r($tablet) {
            width: 100%; }
        input {
            text-align: left;
            @include r($wide) {
                text-align: center; } } }
    .tel {
        border-left: 1px solid rgba(#54b8ee,0.3);
        input {
            padding-left: 35px;
            @include r($wide) {
                padding-left: 5px; } } }
    @include rmin($wide) {
        .name, .tel {
            max-width: 450px; }
        .submit {
            max-width: 280px; } } }
.cleaning-txt--usluga {
    margin-top: 50px;
    padding-bottom: 39px;
    position: relative;
    &::before {
        content: '';
        display: block;
        position: absolute;
        background: url(../img/main-contacts-bg.svg) no-repeat top center / cover;
        top: -131px;
        left: 0;
        bottom: 0;
        width: 100%; }
    .container {
        position: relative;
        z-index: 2; }
    .section-title {
        text-align: left;
        margin-bottom: 28px;
        @include r($tablet) {
            text-align: center; } }
    p {
        margin-bottom: 30px;
        &:last-child {
            margin-bottom: 0; } }
    p, li {
        color: #4d4d4d;
        font-family: "VisbyRoundCF";
        font-size: 18px;
        font-weight: 400;
        line-height: 27px;
        letter-spacing: 1.48px; }
    ul {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap; }
    li {
        width: calc( 100% / 3 - 13.3px );
        padding-top: 57px;
        margin-bottom: 46px;
        position: relative;
        @include r($tablet) {
            width: calc( 50% - 10px ); }
        @include r($mobile) {
            width: 100%;
            margin-bottom: 20px; }
        &::before {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 37px;
            content: url(../img/cleaning-txt--usluga.svg); } } }
.what-included-cleaning--usluga {
    margin-top: 98px;
    position: relative;
    &::before {
        content: '';
        background: url(../img/what-included-cleaning--usluga-bg.svg) no-repeat center;
        display: block;
        width: 607px;
        height: 580px;
        position: absolute;
        top: -115px;
        right: 102px; }
    @include r($tablet) {
        margin-top: 50px; }
    .container {
        position: relative;
        z-index: 2; }
    .section-title {
        margin-bottom: 30px; }
    .what-included-cleaning--usluga__wrap {
        display: flex;
        justify-content: space-between;
        @include r($desktop) {
            flex-direction: column; } }
    .usluga-item {
        width: calc( 50% - 10px );
        @include r($desktop) {
            width: 100%; }
        .usluga-item__title {
            @include r($wide) {
                text-align: center; } } } }
.what-included-cleaning--usluga__r {
    @include rmin($desktop) {
        padding-left: 75px; } }
.what-included-cleaning--usluga__l {
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    background-color: #ffffff;
    .usluga-item__title {
        text-align: center; } }
.usluga-item__title {
    color: #562805;
    font-family: "VisbyRoundCF";
    font-size: 27px;
    font-weight: 900;
    letter-spacing: 1.62px;
    padding: 35px 0 21px 0;
    border-bottom: 1px solid rgba(#000,0.1);
    @include r($tablet) {
        font-size: 24px;
        letter-spacing: 1px; } }
.usluga-item__section {
    padding: 27px 30px;
    display: none;
    &:nth-child(even) {
        background-color: #f2f2f2; }
    &:nth-child(-n+4) {
        display: block; } }
.all-items {
    display: block;
    padding: 30px;
    width: 100%;
    text-align: center;
    color: #00bdb6;
    font-family: "MuseoCyrl";
    font-size: 17px;
    font-weight: 300;
    letter-spacing: 1.02px; }
.usluga-item__section, .usluga-item__l, .usluga-item__r {
    color: #4d4d4d;
    font-family: "MuseoCyrl";
    font-size: 17px;
    font-weight: 300;
    letter-spacing: 1.02px; }
.usluga-item__r {
    font-weight: 700;
    width: 100%;
    max-width: 180px;
    @include r($mobile) {
        max-width: 100%;
        text-align: center;
        margin-top: 4px; } }
.what-included-cleaning--usluga__r {
    .usluga-item__wrap {
        padding-top: 29px; } }
.usluga-item__row {
    margin-bottom: 33px;
    display: flex;
    justify-content: space-between;
    @include r($mobile) {
        flex-direction: column;
        align-items: center;
        margin-bottom: 14px; }
    &:last-child {
        margin-bottom: 0; } }
.what-included-cleaning--usluga__total {
    margin-top: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    @include r($desktop) {
        flex-direction: column;
        align-items: center;
        margin-top: 30px;
        text-align: center; } }
.what-included-cleaning--usluga__total-price {
    color: #562805;
    font-family: "VisbyRoundCF";
    font-size: 20px;
    font-weight: 900;
    letter-spacing: 1.2px;
    margin-right: 34px;
    @include r($desktop) {
        margin-bottom: 14px; }
    @include r($tablet) {
        margin-right: 0; }
    span {
        letter-spacing: 2.4px;
        font-size: 30px; } }
.what-included-cleaning--usluga__total__btn {
    max-width: 255px; }
.steps-work--usluga {
    margin-top: 100px;
    @include r($tablet) {
        margin-top: 50px; }
    .container {
        padding-top: 96px;
        border-top: 1px solid rgba(0, 0, 0, 0.2);
        @include r($tablet) {
            padding-top: 50px; } } }
.reliable-performers-we-use-funds--usluga {
    margin-top: 26px;
    .container {
        border-bottom: none; } }
.our-portfolio--usluga {
    position: relative;
    &::before {
        content: '';
        display: block;
        position: absolute;
        top: -236px;
        left: 0;
        width: 100%;
        height: 966px;
        background: url(../img/our-portfolio--usluga-bg.svg) no-repeat top center; }
    .container {
        position: relative;
        z-index: 2; } }
.section-review--usluga {
    margin-top: 120px;
    @include r($desktop) {
        margin-top: 90px; }
    @include r($tablet) {
        margin-top: 70px; }
    @include r($mobile) {
        margin-top: 50px; }
    .wrapper-review-slider {
        margin-bottom: 100px; } }
.faq.faq--usluga {
    margin-top: 0; }
