.main-reviews {
    padding-top: 230px;
    padding-bottom: 110px;
    @include r($wide) {
        padding-top: 110px; }
    @include r($tablet) {
        padding-bottom: 50px; } }
.main-reviews__tabs-title {
    margin-top: 35px;
    display: flex;
    @include r($desktop) {
        justify-content: center; } }
.main-reviews__tab-title {
    max-width: 205px;
    width: 100%;
    padding: 15px 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #4d4d4d;
    font-family: "VisbyRoundCF";
    font-size: 18px;
    font-weight: 500;
    letter-spacing: 1.08px;
    box-shadow: 0 7px 20px rgba(98, 36, 7, 0.35);
    border-radius: 10px;
    background-color: #622407;
    color: #fff;
    transition: 0.3s;
    position: relative;
    &:first-child {
        margin-right: 20px; } }
.main-reviews__tab-title.active {
    box-shadow: none;
    background-color: rgba(#00bdb6,0.1);
    color: #4d4d4d;
    &::after {
        top: 100%;
        left: 50%;
        transform: translateX(-50%);
        content: '';
        display: block;
        position: absolute;
        border-left: 19px solid transparent;
        border-right: 19px solid transparent;
        border-top: 10px solid rgba(#00bdb6,0.1); } }
.main-reviews__tabs-content {
    margin-top: 50px;
    .review-txt-container {
        margin-bottom: 39px;
        @include r($tablet) {
            margin-bottom: 20px; }
        &:last-child {
            margin-bottom: 0; } } }
.main-reviews__tab-content {
    display: none;
    &:first-child {
        display: block; }
    .btn {
        margin-right: auto;
        margin-left: auto; } }
