.main-404__container {
    padding-top: 205px;
    padding-bottom: 170px;
    position: relative;
    display: flex;
    justify-content: space-between;
    @include r($desktop) {
        padding-top: 100px;
        padding-bottom: 330px; }
    @include r($tablet) {
        padding-bottom: 450px; }
    @include r($mobile) {
        padding-bottom: 300px; } }
.main-404__txt {
    max-width: 655px;
    width: 100%;
    position: relative;
    z-index: 2;
    @include r($desktop) {
        text-align: center;
        margin-left: auto;
        margin-right: auto; } }
.main-404__title {
    color: #00bdb6;
    font-family: "VisbyRoundCF";
    font-size: 250px;
    font-weight: 900;
    text-transform: uppercase;
    letter-spacing: 15px;
    line-height: 0.9;

    margin-bottom: 74px;
    @include r($desktop) {
        margin-bottom: 40px;
        font-size: 200px; }
    @include r($tablet) {
        font-size: 150px; }
    @include r($mobile) {
        font-size: 100px; } }
.main-404__subtitle {
    color: #4d4d4d;
    font-family: "MuseoCyrl";
    font-size: 40px;
    font-weight: 300;
    letter-spacing: 2.4px;
    @include r($mobile) {
        font-size: 25px; } }
.main-404__btn {
    max-width: 280px;
    margin-top: 74px;

    @include r($desktop) {
        margin-left: auto;
        margin-right: auto;
        margin-top: 40px; } }
.main-404__img {
    top: -70px;
    right: -185px;
    display: block;
    position: absolute;
    @include r($wide) {
        right: -260px; }
    @include r($desktop) {
        top: auto;
        bottom: 0;
        width: 500px; }
    @include r($tablet) {
        right: 50%;
        transform: translateX(50%); }
    @include r($mobile) {
        width: 350px; } }
