.our-portfolio {
    .section-title {
        margin-bottom: 30px; } }
.our-portfolio__wrapper-slider {
    position: relative;
    @include r(1400) {
        padding-bottom: 80px; }
    @include r($mobile) {
        padding-bottom: 60px; } }
.our-portfolio__slider {
    .swiper-slide {
        display: flex;
        justify-content: space-between;
        background-color: #fff;
        border-radius: 10px;
        padding: 20px; } }
.our-portfolio__img-wrap {
    height: 316px;
    width: calc( 50% - 10px );
    position: relative;
    @include r($wide) {
        height: 260px; }
    @include r($desktop) {
        height: 240px; }
    @include r($mobile) {
        height: 180px; }
    &:first-child {
        &::before {
            content: 'До'; } }
    &:last-child {
        &::before {
            content: 'После'; } }
    &::before {
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        text-align: center;
        padding: 13px 0;
        min-width: 110px;
        background-color: rgba(#000000,0.5);
        color: #fbfbfb;
        font-family: "VisbyRoundCF";
        font-size: 20px;
        font-weight: 900;
        letter-spacing: 1.2px; }
    img {
        width: 100%;
        height: 100%;
        object-fit: cover; } }
.our-portfolio-button-next, .our-portfolio-button-prev {
    top: 50%;
    transform: translateY(-50%);
    @include r(1400) {
        top: auto;
        bottom: 0; } }
.our-portfolio-button-prev {
    left: -86px;
    @include r(1400) {
        left: 50%;
        transform: translateX(-50%);
        margin-left: -70px; } }
.our-portfolio-button-next {
    right: -86px;
    @include r(1400) {
        right: 50%;
        transform: translateX(50%);
        margin-right: -70px; } }
