.main-contacts {
    background: url(../img/main-contacts-bg.svg) no-repeat top 309px center;
    background-size: cover;
    padding-top: 230px;
    padding-bottom: 128px;
    @include r($wide) {
        padding-top: 150px; }
    @include r($desktop) {
        padding-top: 120px;
        padding-bottom: 50px; }
    @include r($tablet) {
        background-position: top 20px center; }
    @include r($mobile) {
        padding-bottom: 35px; }
    .wrapper-items {
        margin-top: 50px;
        display: flex;
        justify-content: space-between;
        @include r($desktop) {
            flex-wrap: wrap;
            justify-content: center; }
        @include r($tablet) {
            margin-top: 20px; } }
    .item {
        max-width: 280px;
        width: 100%;
        @include r($wide) {
            max-width: 220px; }
        @include r($desktop) {
            max-width: 350px;
            margin: 10px;
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center; } }
    .wrapper-icon {
        width: 150px;
        height: 150px;
        box-shadow: inset 0 5px 0 #d4e6eb;
        background-color: #ffffff;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 20px; }
    .icon {
        height: 53px;
        width: auto;
        max-width: 140px;
        display: block; }
    h4 {
        color: #4d4d4d;
        font-family: "MuseoCyrl";
        font-size: 20px;
        font-weight: 600;
        letter-spacing: 1.2px; }
    p, a {
        color: #4d4d4d;
        font-family: "MuseoCyrl";
        letter-spacing: 1.02px; }
    a {
        &:hover {
            text-decoration: underline; } }
    p {
        margin-top: 21px;
        font-size: 17px;
        font-weight: 400; }
    a[href^="tel"] {
        font-size: 20px;
        font-weight: 600; }
    a[href^="mailto"] {
        color: #00bdb6;
        font-size: 17px;
        font-weight: 300; }
    .map__main-contacts {
        margin-top: 40px;
        height: 699px;
        box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
        border-radius: 10px;
        background-color: #ffffff;
        overflow: hidden;
        @include r($wide) {
            height: 500px; }
        @include r($desktop) {
            height: 400px; }
        @include r($tablet) {
            margin-top: 20px; }
        @include r($mobile) {
            height: 250px; }
        & > ymaps {
            width: 100% !important;
            height: 100% !important; } } }
.still-have-questions-contacts {
    padding: 96px 0 118px 0;
    @include r($desktop) {
        padding: 50px 0; } }

