.footer {
    background-color: #f2fcfb;
    padding: 30px 0;
    position: relative;
    z-index: 2;
    .top-line, .bottom-line {
        display: flex;
        justify-content: space-between;
        @include r($wide) {
            flex-direction: column;
            align-items: center; } }
    .wrapper-logo {
        max-width: 160px;
        width: 100%;
        img {
            width: 100%;
            height: auto;
            display: block; } }
    .txt-line {
        margin-top: 20px;
        text-align: center;
        p {
            margin-bottom: 10px;
            &:last-child {
                margin-bottom: 0; } } }
    .nav-footer {
        margin-left: 28px;
        margin-top: 28px;
        max-width: 375px;
        width: 100%;
        display: flex;
        justify-content: space-between;
        a {
            color: #4d4d4d;
            font-family: "MuseoCyrl";
            font-size: 18px;
            font-weight: 300;
            line-height: 22px;
            letter-spacing: 1.08px;
            &:hover {
                text-decoration: underline; } }
        li {
            margin-bottom: 25px;
            &:last-child {
                margin-bottom: 0; } } }
    .menu-footer-1 {
        max-width: 205px;
        width: 100%; }
    .menu-footer-2 {
        max-width: 150px;
        width: 100%; }
    .attachment {
        margin-top: 28px;
        max-width: 290px;
        width: 100%;
        p {
            text-align: center;
            color: #4d4d4d;
            font-family: "MuseoCyrl";
            font-size: 18px;
            font-weight: 300;
            line-height: 22px;
            letter-spacing: 1.08px;
            margin-bottom: 14px; } }
    .wrapper-links {
        display: flex;
        justify-content: space-between;
        .wrapper-link {
            width: calc( 50% - 10px ); }
        img {
            width: 100%;
            height: auto;
            display: block; } }
    .connection {
        margin-top: 27px;
        max-width: 225px;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-end; }
    .tel {
        color: #562805;
        font-family: "MuseoCyrl";
        font-size: 22px;
        font-weight: 700;
        letter-spacing: 1.32px;
        margin-bottom: 23px; }
    .wrapper-socials {
        width: 100%;
        display: flex;
        justify-content: space-between; }
    .soc-item {
        &:first-child {
            margin-left: 9px; } }
    svg {
        width: 28px;
        height: 28px;
        fill: #cccccc;
        transition: 0.3s;
        &:hover {
            fill: #00bdb6; } }
    .bottom-line {
        // margin-top: 40px
        margin-top: 20px;
        padding-top: 18px;
        display: flex;
        justify-content: space-between;
        border-top: 1px solid rgba(#562805,0.1);
        @include r($wide) {
            margin-top: 20px; }
        p, a {
            color: #666666;
            font-family: "MuseoCyrl";
            font-size: 18px;
            font-weight: 300;
            letter-spacing: 1.08px;
            @include r($tablet) {
                text-align: center; } }
        a {
            text-decoration: underline; } }
    .copy-politic {
        display: flex;
        @include r($wide) {
            margin-bottom: 10px; }
        @include r($tablet) {
            flex-direction: column;
            align-items: center; } }
    .copy {
        @include r($tablet) {
            margin-bottom: 10px; }
        &::after {
            content: '|';
            margin: 0 26px;
            @include r($tablet) {
                display: none; } } } }

