.price-table {
    .section-title {
        margin-bottom: 30px;
        @include r($mobile) {
            margin-bottom: 15px; } } }
.price-table__table {
    border-radius: 10px;
    overflow: hidden;
    background-color: #fff;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1); }
.price-table__title-table {
    color: #ffffff;
    font-family: "VisbyRoundCF";
    font-size: 27px;
    font-weight: 900;
    letter-spacing: 1.62px;
    background-color: #00bdb6;
    padding: 28px 50px 25px 21px;
    cursor: pointer;
    @include r($mobile) {
        text-align: center;
        padding: 10px 25px 10px 25px;
        font-size: 20px; } }
.price-table__icon {
    top: 28px;
    right: 30px;
    position: absolute;
    width: 25px;
    height: 16px;
    @include r($mobile) {
        width: 23px;
        height: 13px;
        top: 11px;
        right: 7px; }
    svg {
        transition: 0.3s;
        fill: #00bdb6;
        width: 100%;
        height: 100%; } }
.price-table__line {
    display: flex;
    justify-content: space-between;
    @include r($mobile) {
        flex-direction: column; }
    &:nth-child(even) {
        background-color: #f2f2f2; } }
.price-table__l, .price-table__r {
    width: 100%;
    padding: 23px 28px;
    color: #4d4d4d;
    font-family: "MuseoCyrl";
    font-size: 17px;
    font-weight: 400;
    letter-spacing: 1.02px;
    @include r($mobile) {
        padding: 8px; } }
.price-table__l {
    @include r($mobile) {
        text-align: center; } }
.price-table__r {
    width: 100%;
    max-width: 280px;
    text-align: center;
    font-weight: 700;
    @include r($mobile) {
        max-width: 100%; } }
