.mfp-wrap {
  -webkit-overflow-scrolling: touch;
  -webkit-transform: translateZ(0); }
html.mfp-helper {
  height: 100%;
  body {
    overflow: hidden;
    height: 100%;
    -webkit-transform: translateZ(0); } }
#small-dialog {
  background: white;
  padding: 20px 30px;
  text-align: left;
  max-width: 600px;
  margin: 40px auto;
  position: relative; }
.mfp-close-btn-in .mfp-close {
  color: transparent;
  right: 0;
  top: -65px;
  width: 35px;
  height: 35px;
  background: url(../img/x.svg) no-repeat center;
  @include r($tablet) {
    top: -25px;
    background-size: 100%;
    width: 20px;
    height: 20px; } }
.mfp-image-holder .mfp-close,
.mfp-iframe-holder .mfp-close {
  width: inherit;
  min-width: 33px; }
.white-popup-block {
  max-width: 730px;
  padding: 47px 55px 73px 55px;
  text-align: center;
  margin: auto;
  position: relative;
  background-color: #ffffff;
  width: 100%;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
  @include r($tablet) {
    padding: 30px 20px 35px 20px; }
  @include r($mobile) {
    padding: 15px 15px 30px 15px; }
  h2 {
    @include r($tablet) {
      font-size: 30px; }
    @include r($mobile) {
      font-size: 25px; } }
  .desc-title {
    margin-top: 21px;
    color: #4d4d4d;
    font-family: "VisbyRoundCF";
    font-size: 25px;
    font-weight: 700;
    letter-spacing: 1.5px;
    margin-bottom: 15px;
    @include r($tablet) {
      font-size: 18px; }
    @include r($tablet) {
      margin-top: 10px;
      margin-bottom: 10px; } }
  form {
    max-width: 505px;
    width: 100%;
    margin-left: auto;
    margin-right: auto; }
  input[type="text"], input[type="tel"] {
    height: 50px;
    padding-left: 0;
    border-radius: 0;
    border: none;
    border-bottom: 1px solid rgba(#000,0.2);
    color: #4d4d4d;
    letter-spacing: 1.08px; }
  input[type="text"] {
    font-family: "VisbyRoundCF";
    font-size: 18px;
    font-weight: 300;
    line-height: 27px; }
  input[type="tel"] {
    color: #4d4d4d;
    font-family: "MuseoCyrl";
    font-size: 18px;
    font-weight: 300;
    line-height: 22px; }
  p {
    color: #4d4d4d;
    font-family: "MuseoCyrl";
    font-weight: 300;
    strong {
      font-weight: 700;
      font-size: 18px;
      letter-spacing: 1.2px; } }
  .submit {
    margin-bottom: 0; }
  .politic {
    letter-spacing: 0.96px;
    font-size: 16px;
    line-height: 22px; }
  .btn {
    max-width: 280px;
    width: 100%;
    margin-top: 35px;
    margin-left: auto;
    margin-right: auto;
    @include r($tablet) {
      margin-top: 10px; } } }
.popup-buble {
  .popup-dialog {
    opacity: 0;
    transition: all 0.2s ease-in-out;
    transform: scale(0.8); }
  &.mfp-ready .popup-dialog {
    opacity: 1;
    transform: scale(1); }
  &.mfp-removing .popup-dialog {
    transform: scale(0.8);
    opacity: 0; }
  &.mfp-bg {
    background: rgba(#fff, 0.8);
    opacity: 0;
    transition: 0.3s; }
  &.mfp-ready.mfp-bg {
    opacity: 1; }
  &.mfp-removing.mfp-bg {
    opacity: 0; } }
.modal-form-tnx {
  .popup-dialog {} }
.modal-advantages {
  padding: 50px 40px 58px 40px;
  @include r($tablet) {
    padding: 15px; }
  .popup-dialog {
    height: 650px;
    overflow-y: auto;
    @include r($tablet) {
      height: 450px; }
    h2 {
      margin-bottom: 24px; }
    p {
      font-size: 17px;
      text-align: left;
      margin-bottom: 23px;
      &:last-child {
        margin-bottom: 0; } } }
  .wrapper-icon {
    max-width: 130px;
    width: 100%;
    margin-top: 51px;
    margin-left: auto;
    margin-right: auto;
    img {
      width: 100%;
      height: auto;
      display: block; } } }
#modal-form-tnx {
  .thanks {
    margin-top: 35px;
    max-width: 238px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    @include r($tablet) {
      max-width: 190px;
      margin-top: 10px; } } }
