.house-order-longer-your-concern_abous-us {
    padding-bottom: 110px;
    position: relative;
    @include r($tablet) {
        padding-bottom: 50px; }
    &::after {
        position: absolute;
        left: 0;
        bottom: 0;
        content: '';
        display: block;
        width: 100%;
        height: 792px;
        background: url(../img/house-order-longer-your-concern__about-us_bg.svg) no-repeat center;
        @include r($wide) {
            display: none;
            background-image: none; } }
    .container {
        position: relative;
        z-index: 2; } }
.steps-work {
    padding-top: 76px;
    @include r($wide) {
        padding-top: 0; }
    .container {
        padding-bottom: 96px;
        border-bottom: 1px solid rgba(#000,0.2);
        @include r($desktop) {
            padding-bottom: 50px; } } }
.steps-work__wrap {
    margin-top: 65px;
    display: flex;
    justify-content: space-between;
    counter-reset: counter;
    position: relative;
    @include r($desktop) {
        flex-wrap: wrap; }
    @include r($mobile) {
        margin-top: 35px; } }
.steps-work__item {
    width: 25%;
    position: relative;
    z-index: 2;
    padding: 0 14px;
    @include r($desktop) {
        width: 50%;
        padding: 14px; }
    @include r($mobile) {
        width: 100%; }
    &:last-child {
        .wrapper-icon {
            &::after {
                display: none; } } }
    .wrapper-icon {
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 20px;
        width: 154px;
        height: 154px;
        border: 1px dashed #cdcdcd;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        &::before {
            position: absolute;
            top: -9px;
            right: 10px;
            content: counter(counter);
            counter-increment: counter;
            width: 40px;
            height: 40px;
            background-color: #00bdb6;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            color: #ffffff;
            font-family: "VisbyRoundCF";
            font-size: 26px;
            font-weight: 900;
            letter-spacing: 1.56px;
            padding: 0;
            text-indent: 3px; }
        &::after {
            display: block;
            left: 100%;
            content: '';
            background-color: #bcbcbc;
            height: 1px;
            width: 84px;
            position: absolute;
            top: 75px;
            @include r($wide) {
                width: 42px; }
            @include r($desktop) {
                display: none; } } }
    img {
        max-width: 100%;
        height: 75px;
        width: auto;
        display: block; }
    p {
        text-align: center;
        color: #4d4d4d;
        font-family: "VisbyRoundCF";
        font-size: 18px;
        font-weight: 300;
        line-height: 27px;
        letter-spacing: 1.08px; } }
.reliable-performers-we-use-funds.we-use-funds-about-us {
    margin-top: 25px;
    .container {
        border-bottom: none;
        padding-bottom: 0; } }
.reliable-performers-we-use-funds.reliable-performers-about-us {
    margin-top: -68px;
    padding-bottom: 130px;
    @include r($wide) {
        padding-bottom: 70px; }
    @include r($desktop) {
        margin-top: -10px;
        padding-bottom: 40px; }
    &::before {
        background-position: top center; }
    .container {
        border-top: none; } }
.faq-about-us {
    margin-top: 20px;
    margin-bottom: 150px;
    @include r($wide) {
        margin-bottom: 120px; }
    @include r($desktop) {
        margin-bottom: 80px; } }


