.banner {
    padding: 88px 0;
    @include r($tablet) {
        padding: 45px 0; }
    @include r($mobile) {
        padding: 15px 0; }
    .container {
        @include r($tablet) {
            padding: 0; } } }
.banner__img {
    width: 100%;
    height: auto;
    display: block;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
    @include r($mobile) {
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        width: calc( 100% + 20px );
        box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1); } }
