input,
input[type="text"],
input[type="tel"],
input[type="email"], {
  border-radius: 4px;
  border: 1px solid #e1e1e1;
  background-color: #ffffff;
  color: #232323;
  font-family: $font-family;
  font-size: 16px;
  font-weight: 300;
  letter-spacing: 0.8px;
  padding: 10px 20px;
  -webkit-appearance: none;
  &::-webkit-input-placeholder {
    color: rgb(51, 51, 51); }
  &::-moz-placeholder {
    color: rgb(51, 51, 51); }
  &:-moz-placeholder {
    color: rgb(51, 51, 51); }
  &:-ms-input-placeholder {
    color: rgb(51, 51, 51); } }
.ui-group {
  position: relative;
  margin-bottom: 20px;
  input, textarea {
    width: 100%; } }
input[type="number"] {
  -moz-appearance: textfield; }
input[type="number"]::-webkit-inner-spin-button, input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0; }
