.section-review {
    padding-bottom: 100px;
    @include r($tablet) {
        padding-bottom: 50px; }
    h2 {
        margin-bottom: 24px; }
    .desc-title {
        text-align: center;
        color: #4d4d4d;
        font-family: "VisbyRoundCF";
        font-size: 18px;
        font-weight: 500;
        letter-spacing: 1.08px; }
    .swiper-container {
        position: relative;
        max-width: 880px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
        border-radius: 23px;
        background-color: #fff; }
    .wrapper-slider2 {
        max-width: 880px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        position: relative;
        @include r($wide) {
            max-width: 780px; }
        @include r($desktop) {
            max-width: 600px; } }
    .wrapper-review-slider {
        margin-top: 30px;
        @include r($tablet) {
            padding-bottom: 90px; }
        .wrapper-slider2 {
            &::before {
                top: 30px;
                right: 30px;
                left: 30px;
                bottom: -30px;
                position: absolute;
                content: '';
                display: block;
                box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
                border-radius: 10px;
                background-color: #622407;
                opacity: 0.05;
                @include r($tablet) {
                    display: none; } }
            &::after {
                top: 60px;
                right: 60px;
                left: 60px;
                bottom: -60px;
                position: absolute;
                content: '';
                display: block;
                box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
                border-radius: 10px;
                background-color: #622407;
                opacity: 0.05;
                @include r($tablet) {
                    display: none; } } }
        .swiper-slide {
            padding: 40px 30px 55px 30px;
            @include r($tablet) {
                padding: 20px 15px; } } }
    .review-button-next, .review-button-prev {
        top: 50%;
        transform: translateY(-50%);
        @include r($tablet) {
            transform: none;
            top: auto;
            bottom: 0; } }
    .review-button-next {
        right: 0;
        @include r($tablet) {
            right: 50%;
            transform: translateX(50%);
            margin-right: -70px; } }
    .review-button-prev {
        left: 0;
        @include r($tablet) {
            left: 50%;
            transform: translateX(-50%);
            margin-left: -70px; } }
    .wrapper-content {
        text-align: center;
        padding: 0 17px;
        @include r($tablet) {
            padding: 0; } }
    .wrapper-info {
        border-top: 1px solid rgba(#000,0.1);
        padding-top: 28px;
        margin-top: 28px;
        display: flex;
        justify-content: space-between;
        @include r($tablet) {
            flex-direction: column;
            align-items: center; } }
    .item {
        max-width: 185px;
        width: 100%;
        @include r($desktop) {
            margin-bottom: 10px; }
        @include r($tablet) {
            max-width: 100%;
            text-align: center; }
        h4 {
            color: #4d4d4d;
            font-family: "MuseoCyrl";
            font-size: 17px;
            font-weight: 400;
            line-height: 24px;
            color: #999999;
            letter-spacing: 1.02px;
            margin-bottom: 9px;
            @include r($desktop) {
                margin-bottom: 2px; } }
        &:nth-child(2) {
            @include rmin($desktop) {
                margin-left: 50px; } }
        img {
            max-width: 100%;
            height: auto; } }
    p {
        color: #4d4d4d;
        font-family: "MuseoCyrl";
        font-size: 17px;
        font-weight: 300;
        line-height: 24px;
        letter-spacing: 1.02px;
        strong {
            font-weight: 900;
            margin-right: 15px; } }
    .wrapper-video-slider {
        @include r($tablet) {
            padding-bottom: 90px; }
        .wrapper-slider2 {
            &::before {
                top: 20px;
                right: 20px;
                left: -20px;
                bottom: -20px;
                position: absolute;
                content: '';
                display: block;
                border-radius: 20px;
                background-color: #622407;
                opacity: 0.1;
                @include r($tablet) {
                    display: none; } } }
        .swiper-slide {
            height: 495px;
            position: relative;
            @include r($tablet) {
                height: 350px; }
            @include r($mobile) {
                height: 240px; } }
        .preview-img {
            width: 100%;
            height: 100%;
            object-fit: cover; }
        iframe {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 2; }
        .play {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            width: 119px;
            height: 119px;
            background-color: rgba(#000000,0.1);
            border-radius: 50%;
            opacity: 0.8;
            transition: 0.3s;
            &::before {
                display: block;
                content: '';
                position: absolute;
                z-index: 1;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%);
                width: 85px;
                height: 85px;
                background-color: #000000;
                opacity: 0.1;
                border-radius: 50%; }
            &::after {
                display: block;
                content: url(../img/play.svg);
                width: 29px;
                position: absolute;
                z-index: 2;
                top: calc( 50% + 2px );
                left: calc( 50% + 2px );
                transform: translate(-50%,-50%); }
            &:hover {
                opacity: 1; } } }
    .video-button-next, .video-button-prev {
        top: 50%;
        transform: translateY(-50%);
        @include r($tablet) {
            transform: none;
            top: auto;
            bottom: 0; } }
    .video-button-next {
        right: 0;
        @include r($tablet) {
            right: 50%;
            transform: translateX(50%);
            margin-right: -70px; } }
    .video-button-prev {
        left: 0;
        @include r($tablet) {
            left: 50%;
            transform: translateX(-50%);
            margin-left: -70px; } }
    .btn {
        margin: 51px auto 0 auto;
        max-width: 280px; } }
